:root {
  --light-gray: #EEEEEE;
  --dark-gray: #989898;
  --darker-gray: #676767;
  --blue: #390FAE;
  --dark-blue: #371596;
  --darker-blue: #23096D;
  font-family: Poppins;
}

html, body, #root {
  width: 100%;
  height: 100%;
}

input, button, select {
  font-family: Poppins;
}

body { 
  margin: 0;
}

*:focus {
  outline: none;
}

.basic-container {
  display: flex;
  height: 100%;
  width: 100%;
}

.basic-second-container {
  display: flex;
  height: 100%;
  width: 100%;
  overflow-y: hidden;
}

.start {
  align-items: center;
}