.form-input-container {
  background-color: white;
  display: flex;
  align-items: center;
  color: var(--dark-gray);
  background-color: #F8F8F8;
  padding-left: 18px;
  padding-right: 18px;
  padding-top: 11px;
  padding-bottom: 11px;
  margin: 8px;
  margin-top: 12px;
  margin-bottom: 12px;
  border-radius: 14px;
}

.form-input-container * {
  background-color: transparent;
}

.form-input-container:hover {
  border-radius: 1px solid black;
}

textarea {
  font-family: Poppins;
}

.form-input-container svg {
  min-width: 25px;
}

.form-input-container select {
  width: 100%;
  background-color: transparent;
}

.form-input {
  color: black;
  border: 0;
  font-size: 18px;
  margin-left: 8px;
  width: 100%;
}

.form-select {
  padding-top: 1px;
  padding-bottom: 1px;
}

.form-input::placeholder {
  color: var(--dark-gray);
}

.form-button {
  color: white;
  background-color: var(--blue);
  border: 0;
  font-size: 18px;
  padding-left: 88px;
  padding-right: 88px;
  padding-top: 11px;
  padding-bottom: 11px;
  margin-left: 8px;
  margin-right: 8px;
  transition: 0.5s background-color;
  cursor: pointer;
  border-radius: 14px;
}

.form-button:hover {
  background-color: var(--darker-blue);
}

.form-checkbox-container {
  display: flex;
  align-items: center;
  margin-left: 4px;
  margin-right: 8px;
  margin-top: 11px;
  margin-bottom: 11px;
}

.form-checkbox-container input[type="checkbox"] {
  width: 20px;
  min-width: 20px;
  height: 22px;
  min-height: 22px;
  margin-right: 8px;
}

.form-checkbox-text {
  color: var(--darker-gray);
  font-size: 17px;
}

.container {
  display: block;
  position: relative;
  padding-left: 35px;
  height: 100%;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  
}

/* Hide the browser's default checkbox */
.container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
  border-radius: 15px;
}

/* On mouse-over, add a grey background color */
.container:hover input ~ .checkmark {
  background-color: #ccc;
}

/* When the checkbox is checked, add a blue background */
.container input:checked ~ .checkmark {
  background-color: #2196F3;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.container .checkmark:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

textarea {
  min-height: 150px;
}