.activity {
  flex-direction: row;
  justify-content: center;
}
  
.activity-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 12px;
  padding-left: 0;
  padding-right: 0;
  padding-top: 32px;
  width: calc(70% - 32px) !important;
  max-width: 900px;
  overflow-y: auto;
  position: relative;
}

.activity-block img {
  width: fit-content;
  max-width: 100%;
}

.activity-block::-webkit-scrollbar {
  width: 8px;
}
    
.activity-block::-webkit-scrollbar-track {
  background: var(--light-gray);
}
    
.activity-block::-webkit-scrollbar-thumb {
  background: var(--dark-gray);
}
      
.activity-block::-webkit-scrollbar-thumb:hover {
  background: #888;
}  

.activity p {
  text-align: justify;
  color: var(--darker-gray);
  margin-left: 12px;
  margin-right: 12px;
  line-height: 28px;
  width: calc(100% - 24px);
}

.activity .course-playlist {
  width: 30%;
}
  
.activity .course-topics-list-block h2 {
  font-size: 16px;
}
  
.activity .course-topics-list-number-container {
  margin-bottom: 16px;
}
  
.activity .course-topics-list-number {
  font-size: 14px;
  color: var(--dark-gray);
}
    
.activity .course-video {
  font-size: 16px;
}

.activity-alternatives {
  margin-top: 8px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.activity-alternatives .form-checkbox-container {
  width: calc(100% - 44px);
  padding: 18px;
  margin: 4px;
}

.activity-alternatives .form-checkbox-text {
  color: #272727;
  font-size: 16px;
  margin-left: 12px;
}

.activity-alternatives button {
  margin-top: 12px;
}

.activity-block hr {
  width: calc(100% - 24px);
}

.activity-block-header {
  display: flex;
  align-items: center;
  justify-content: center;
}

.activity-block-header h2 {
  margin-bottom: 0;
  margin-top: 0;
  margin-left: 8px;
  margin-right: 8px;
}

.alternative-corrected span {
  color: var(--darker-blue) !important;
}

.alternative-corrected {
  border-radius: 8px;
  background-color: #2f9e4145;
  font-weight: 600;
}

.alternative-wrong {
  border-radius: 8px;
  background-color: #ff00002e;
  font-weight: 600;
}

.alternative-wrong span {
  color: red !important;
}

.activity-modal button {
  width: calc(100% - 8px);
  margin: 4px;
  max-width: unset;
}

.activity-text {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (max-width: 728px) {
  .activity {
    flex-direction: column;
    justify-content: unset;
  }

  .activity-block {
    width: 100% !important;
    margin: 0;
    min-height: 60%;
    max-height: fit-content;
    margin-bottom: 12px;
    margin-top: 18px;
  }
}