.course-list {
  display: flex;
  overflow-x: auto;
  padding-bottom: 12px;
}

.courses-swiper * {
  overflow-y: hidden;
}

.course-list::-webkit-scrollbar {
  height: 8px;
}

.course-list::-webkit-scrollbar-track {
  background: var(--light-gray);
}

.course-list::-webkit-scrollbar-thumb {
  background: var(--dark-gray);
}

.course-list::-webkit-scrollbar-thumb:hover {
  background: #888;
}

.course-item-list {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  margin-right: 8px;
  user-select: none;
  cursor: pointer;
  transition: 0.5s all background-color;
  border-radius: 12px;
  padding: 6px;
  max-width: 350px;
  transition: 0.2s all;
}

.course-item-list:hover {
  transform: scale(1.03);
  background-color: #f2f2f2;
}

.course-item-list:first-child {
  margin-left: 0;
}

.course-item-list b {
  margin-top: 4px;
}

.course-item-list b, .course-item-list span {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.course-item-list span {
  color: var(--darker-gray);
}

.course-item-list-image {
  width: 350px;
  height: 193px;
  background-color: var(--light-gray);
  background-position: center;
  background-size: cover;
  display: flex;
  align-items: flex-end;
  border-radius: 12px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  position: relative;
  box-shadow: 0px 0px 2px 1px rgb(0 0 0 / 25%);
}

.course-item-list-university {
  position: absolute;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 12px;
  left: 12px;
  border-radius: 8px;
  background-color: white;
  box-shadow: 0px 0px 2px 1px rgb(0 0 0 / 25%);
}

.course-item-list-university img {
  height: 38px;
}

