.course-item-list-image-percentage {
  height: 8px;
  background-color: var(--dark-blue);
  border-bottom-left-radius: 12px;
}

.course-item-list-text {
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  box-shadow: 0px 0px 2px 1px rgb(0 0 0 / 25%);
  padding-left: 12px;
  padding-right: 12px;
  padding-top: 12px;
  padding-bottom: 12px;
  background-color: white;
  overflow: hidden;
  text-overflow: ellipsis;
}

.course-item-list-text b {
  font-weight: 500;
  color: var(--blue);
  font-size: 17px;
  white-space: nowrap;
}

.course-item-list-text p {
  margin-top: 4px;
  margin-bottom: 0;
  color: var(--darker-gray);
  width:100%;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}