.courses-swiper > div:last-child {
  display: flex;
  align-items: center;
}

.icon-container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  height: 100%;
  padding: 12px;
  z-index: 3;
  top: 0;
  color: black;
  cursor: pointer;
}

.icon-container::after {
  background-color: rgba(255, 255, 255, 0.6);
  filter: blur(12px);
  content: '';
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.icon-container * {
  z-index: 1;
}

.left-icon {
  left: 0;
}

.right-icon {
  right: 0;
}