.modal-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9;
}

.modal {
  background-color: white;
  border-radius: 12px;
  padding: 8px;
  width: 95%;
  max-width: 600px;
}

.modal-close {
  position: absolute;
  top: 12px;
  right: 12px;
  color: white;
  cursor: pointer;
  user-select: none;
}