.video {
  flex-direction: row;
  justify-content: center;
}

.video-block::-webkit-scrollbar {
  width: 8px;
}

.video-block::-webkit-scrollbar-track {
  background: var(--light-gray);
}

.video-block::-webkit-scrollbar-thumb {
  background: var(--dark-gray);
}
  
.video-block::-webkit-scrollbar-thumb:hover {
  background: #888;
}


.video-block {
  margin-right: 12px;
  padding-top: 32px;
  padding-left: 0;
  padding-right: 0;
  width: calc(70% - 32px) !important;
  max-width: 900px;
  overflow-y: auto;
}

.video-iframe-container {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
}

.video-block iframe {
  position: absolute;
  top: 0; 
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
  border-radius: 12px;
}

.video-block-info {
  padding: 24px;
  line-height: 25px;
}

.video-block-info p {
  color: var(--darker-gray);
  text-align: justify;
}

.video-block-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.video-block-title input[type="checkbox"] {
  width: 18px;
  height: 18px;
}

.video-block-title .form-checkbox-text {
  font-size: 18px;
  color: var(--darker-gray);
}

.video-block-info-resources {
  display: flex;
  align-items: center;
}

.download-resource {
  color: var(--darker-blue);
  display: flex;
  align-items: center;
  font-size: 18px;
  margin-left: 24px;
  cursor: pointer;
  user-select: none;
}

.download-resource:first-child {
  margin-left: 0;
}

.download-resource span {
  margin-left: 6px;
  font-weight: 600;
}

.video .course-playlist {
  width: 30%;
}

.video .course-topics-list-block h2 {
  font-size: 16px;
}

.video .course-topics-list-number-container {
  margin-bottom: 16px;
}

.video .course-topics-list-number {
  font-size: 14px;
  color: var(--dark-gray);
}
  
.video .course-video {
  font-size: 16px;
}

@media screen and (max-width: 728px) {
  .video {
    flex-direction: column;
    justify-content: unset;
  }

  .video-block {
    width: 100% !important;
    margin: 0;
    min-height: 60%;
    max-height: fit-content;
    margin-bottom: 12px;
    margin-top: 18px;
  }
}