.block-start-all {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.block-start-all a {
  text-decoration: none;
  color: var(--darker-gray);
  margin-top: 4px;
  transition: 0.5s color;
  margin-right: 35px;
  font-weight: 600;
}

.block-start-all a:hover {
  color: var(--darker-gray);
}