.start {
  display: flex;
  height: 100%;
  overflow-x: hidden;
  position: relative;
}
  
  
.start-container-left {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 48%;
  height: 100%;
  position: relative;
}
  
.start-form-title {
  color: var(--blue);
  margin-left: 8px;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 28px;
}

.start-form-description {
  color: var(--darker-gray);
  font-size: 17px;
  margin-left: 8px;
  margin-bottom: 8px;
  margin-top: 0px;
  margin-bottom: 16px;
}
  
.start-form {
  width: 80%;
}
  
.start-form-button-container {
  display: flex;
  justify-content: flex-end;
}
  
.start-container-right {
  color: white;
  font-size: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 52%;
  height: 100%;
  padding-left: 32px;
  padding-right: 32px;
  position: relative;
  user-select: none;
  border-top-left-radius: 24px;
  border-bottom-left-radius: 24px;
  background-image: url(https://instrue.com.br/img/background.png);
  background-size: cover;
  background-position: center;
}

.start-container-right::after {
  content: '';
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  position: absolute;
  top: 0;
  left: 0;
  border-top-left-radius: 24px;
  border-bottom-left-radius: 24px;
}

.start-container-right span {
  font-size: 84px;
  z-index: 2;
}
  
@media only screen and (max-width: 1400px) {
  .start-container-right span {
    font-size: 64px;
  }
}
  
@media only screen and (max-width: 1080px) {
  .start-container-right span {
    font-size: 54px;
  }
}
  
@media only screen and (max-width: 930px) {
  .start-container-left {
    width: 100%;
  }
  
  .start-form {
    width: 95%;
  }
  
  .start-container-right {
     display: none;
  }
}

.start-details {
  display: flex;
  flex-direction: column;
  color: var(--dark-blue);
  position: absolute;
}

.start-details-row {
  display: flex;
  line-height: 12px;
}

.start-details-top {
  top: 8px;
  left: 8px;
}

.start-details-bottom {
  bottom: 8px;
  right: 8px;
}