.basic-screen {
  display: flex;
  flex-direction: column;
}

.basic-screen-top {
  display: flex;
  height: 60px;
  padding: 8px;
  background-color: white;
  align-items: center;
  color: var(--dark-gray);
  box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.25);
  z-index: 1;
}

.basic-screen-explore-button {
  width: 134px;
  height: 38px;
  background: var(--blue);
  border-radius: 8px;
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 500;
  font-size: 17px;
  color: white;
  border: 0;
  margin-right: 12px;
  cursor: pointer;
}

.basic-screen-search-container {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.basic-screen-search {
  width: 60%;
  max-width: 600px;
  border-radius: 8px;
  background-color: var(--light-gray);
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 4px;
  padding-left: 12px;
  padding-right: 8px;
}

.basic-screen-search input[type="text"] {
  border: none;
  background-color: transparent;
  flex: 1;
  height: 25px;
  color: var(--dark-gray);
  font-size: 18px;
  width: 100%;
}

.basic-screen-inner {
  display: flex;
  flex-direction: column;
  width: calc(100%);
  height: calc(100%);
  overflow: auto;
}

.basic-screen-avatar {
  margin-right: 27px;
  border-radius: 100px;
  width: 38px;
}

.basic-screen-search form {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.basic-screen-search button {
  border: 0;
  height: 30px;
  color: var(--dark-gray);
  margin: 0;
  padding-right: 0;
}

.basic-screen-top-home {
  cursor: pointer;
  display: none;
}

.basic-screen-top h1 {
  color: var(--blue);
  margin: 0;
  margin-left: calc(35px - 8px);
  font-weight: 700;
  font-size: 32px;
  cursor: pointer;
}

.basic-screen-top-right {
  display: flex;
  align-items: center;
  justify-content: center;
}

.basic-screen-top-right span {
  margin-right: 16px;
  font-weight: 600;
  color: var(--dark-gray);
  cursor: pointer;
  user-select: none;
  transition: 0.5s color;
}

.basic-screen-top-right span:hover {
  color: var(--blue);
}

@media screen and (max-width: 728px) {
  .basic-screen {
    max-width: unset;
    width: 100%;
  }

  .basic-screen-top-home {
    display: block;
  }
}