.block {
  border-radius: 12px;
  padding: 16px;
  padding-right: 0;
  padding-left: 35px;
  margin-top: 16px;
  margin-bottom: 16px;
  width: calc(100% - 35px);
}

.block-title {
  color: #101010;
  margin-top: 4px;
  margin-bottom: 11px;
  font-weight: 600;
  font-size: 26px;
  margin-left: 0;
}