.topic {
  flex-direction: column;
  align-items: center;
  justify-content: unset;
  width: calc(100% - 36px);
  margin-left: 18px;
  margin-right: 18px;
}

.course-vertical-block {
  display: flex;
  height: fit-content;
  cursor: pointer;
  transition: 0.5s filter;
  box-shadow: 0px 0px 2px 1px rgb(0 0 0 / 25%);
  width: 90%;
  max-width: 1280px;
}

.course-vertical-block:hover {
  filter: brightness(0.9);
}

.course-vertical-block .info {
  display: flex;
  flex-direction: column;
  margin-left: 12px;
  margin-top: 8px;
}

.course-vertical-block span {
  color: var(--darker-gray);
}

@media screen and (max-width: 728px) {
  .course-vertical-block {
    flex-direction: column;
    align-items: center;
    padding-left: 0;
    width: calc(100% - 2px);
  }

  .course-vertical-block .info {
    margin-left: 0;
    text-align: center;
    margin-top: 24px;
    margin-bottom: 8px;
  }
}