.course-playlist {
  width: 40%;
  min-width: 350px;
  overflow-y: auto;
  padding-right: 6px;
  height: calc(100% - 51px);
}

.course-playlist::-webkit-scrollbar {
  width: 8px;
}

.course-playlist::-webkit-scrollbar-track {
  background: var(--light-gray);
}

.course-playlist::-webkit-scrollbar-thumb {
  background: var(--dark-gray);
}
  
.course-playlist::-webkit-scrollbar-thumb:hover {
  background: #888;
}

.course-topics-list-block {
  position: relative;
}

.course-topics-list-block h2 {
  margin-bottom: 0px;
  font-size: 20px;
}

.course-topics-list-number-container {
  margin-bottom: 18px;
}

.course-topics-list-number {
  font-size: 18px;
  color: var(--dark-gray);
}
  
.course-video {
  color: var(--darker-gray);
  display: flex;
  flex-direction: column;
  font-size: 18px;
  cursor: pointer;
  box-shadow: rgb(238 238 238 / 80%) 0px 0px 0px 2px;
  padding: 1.6rem 2rem;
  border-radius: 8px;
  margin-bottom: 8px;
  position: relative;
}

.course-video:hover {
  background-color: var(--light-gray);
}
  
.course-video b {
  color: black;
}

.course-video-finished {
  background-color: #2b8e3b0f;
  border: 1px solid var(--dark-blue);
}

.course-video-finished:hover {
  background-color: #2b8e3b2b;
}

.course-video-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.course-video-finished-icon {
  color: var(--dark-blue);
  position: absolute;
  cursor: pointer;
  top: 12px;
  right: 12px;
}