.course-show {
  flex-direction: row;
  justify-content: center;
}

.course-block {
  padding-left: 0;
  padding-right: 0;
  padding-top: 32px;
  margin-right: 22px;
  max-width: 700px;
  overflow: auto;
}

.course-block::-webkit-scrollbar {
  width: 8px;
}

.course-block::-webkit-scrollbar-track {
  background: var(--light-gray);
}

.course-block::-webkit-scrollbar-thumb {
  background: var(--dark-gray);
}
  
.course-block::-webkit-scrollbar-thumb:hover {
  background: #888;
}

.course-block img {
  width: 100%;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.course-show .course-info {
  padding: 16px;
  padding-top: 0;
  height: unset;
  width: unset;
  margin-left: 0;
  padding-left: 1;
  display: flex;
  flex-direction: column;
}

.course-show .course-info h2 {
  margin-top: 8px;
  margin-bottom: 0px;
}

.course-show .course-info span {
  color: var(--dark-gray);
  font-size: 18px;
}

.course-show .course-playlist {
  width: 40%;
}

.add-author {
  color: var(--dark-blue) !important;
  user-select: none;
  cursor: pointer;
}

.course-modal {
  text-align: center;
  overflow-y: auto;
  max-height: 95%;
}

.form-input {
  font-family: Poppins;
}

.course-publish {
  color: var(--dark-blue) !important;
  cursor: pointer;
}

@media screen and (max-width: 728px) {
  .course {
    flex-direction: column;
    justify-content: unset;
  }

  .course-block {
    width: 100% !important;
    margin: 0;
    height: fit-content;
    min-height: calc(60% - (12px + 18px));
    max-height: fit-content;
    margin-bottom: 12px;
    margin-top: 18px;
  }

  .course-playlist {
    width: calc(100% - 6px) !important;
    margin: 0;
    min-height: calc(60% - 12px);
    margin-bottom: 12px;
    padding: 0;
    min-width: unset;
  }

  .video-block-title {
    flex-direction: column;
  }
}

.course {
  padding: 0;
  width: 100%;
  height: 100%;
}

.course-image {
  width: 100%;
  min-height: 450px;
  padding-top: 24px;
  padding-bottom: 24px;
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.course-info img {
  height: 60px;
  position: absolute;
  bottom: 32px;
}

.course-image::after {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  content: '';
  height: 100%;
  position: absolute;
}

.course-title h2 {
  font-weight: 700;
  font-size: 32px;
  color: white;
  margin: 0;
}

.course-title span {
  font-weight: 500;
  font-size: 22px;
  margin-bottom: 12px;
  color: #EBEBEB;
}

.course-title p {
  font-size: 19px;
  max-width: 537px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  color: #E2E2E2;
}

.course-container {
  width: 100%;
  max-width: 1580px;
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
}

.course-info {
  width: 70%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 80px;
  z-index: 2;
}

.course-click {
  width: 100%;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid #d8d8d8;
  border-top: 1px solid #d8d8d8;
  overflow-x: auto;
  min-height: 73px;
  overflow-y: hidden;
  position: sticky;
  top: 0;
}

.course-click a {
  padding: 16px;
  padding-top: 22px;
  padding-bottom: 22px;
  cursor: pointer;
  user-select: none;
  text-decoration: none;
  color: var(--darker-gray);
}

.course-click-selected {
  font-weight: bold;
  color: var(--dark-blue) !important;
  border-bottom: 5px solid var(--dark-blue);
}

.course-body {
  display: flex;
  flex-direction: column;
  background-color: white;
}

.course-title {
  color: white !important;
  max-width: 60%;
}

.course-get {
  background-color: white;
  padding: 12px;
  width: 25%;
  max-width: 320px;
  box-shadow: 0 2px 4px 0 rgb(0 0 0 / 50%);
  z-index: 2;
  margin-right: 80px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 34px 18px;
}

.course-get p {
  color: #363636;
  text-align: center;
  margin-left: 12px;
  margin-right: 12px;
  font-size: 15px;
  margin-top: 0;
  margin-bottom: 16px;
}

.course-get h2 {
  font-size: 20px;
  margin-bottom: 12px;
  font-weight: 700;
  color: black;
  text-align: center;
}

.course-get button {
  width: calc(100% - 24px);
  min-width: none;
  margin-left: 12px;
  margin-left: 12px;
  margin-bottom: 4px;
}

.course-body {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.course-body-container {
  width: calc(100% - 160px);
  max-width: calc(1580px - 160px);
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  padding-left: 80px;
  padding-right: 80px;
}

.course-body-text {
  width: 70%;
  padding: 12px;
  text-align: justify;
}

.course-body-text h2 {
  color: var(--darker-blue);
}

.course-body-text p {
  color: var(--darker-gray);
}

.course-body-info {
  width: 30%;
}

.course-body-info {
  height: 100%;
}

.course-body-info {
  margin-top: calc(12px + 19px + 35px + 20px);
  padding-left: 26px;
}

.course-body-info-item {
  display: flex;
  align-items: center;
  color: var(--darker-gray);
  font-size: 1.3em;
  margin-bottom: 12px;
  color: var(--darker-blue);
}

.course-body-info-item svg {
  margin-right: 12px;
  font-size: 24px;
  min-width: 24px;
  min-height: 24px;
  place-self: flex-start;
  margin-top: 4px;
}

.course-body-info-item p {
  font-size: 14px;
  color: var(--darker-gray);
}

.course-teacher {
  display: flex;
  align-items: center;
  width: 100%;
  margin-top: 16px;
  margin-bottom: 16px;
  border-radius: 12px;
  box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.25);
  width: calc(100% - 58px);
  padding: 25px 29px;
  justify-content: center;
}

.course-teacher img, .course-institute img {
  width: 150px;
  height: 150px;
  border-radius: 200px;
  margin-right: 24px;
  place-self: flex-start;
}

.course-institute img {
  width: unset;
  border-radius: unset;
}

.course-teacher span, .course-institute span {
  color: black;
  font-weight: 700;
  font-size: 24px;
  cursor: pointer;
}

.course-teacher p, .course-institute p {
  font-size: 15px;
}

.curricular-item {
  width: calc(100% - 58px);
  border-radius: 12px;
  box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.25);
  margin-top: 12px;
  padding: 25px 29px;
  display: flex;
  flex-direction: column;
}

.curricular-item h3 {
  margin-top: 8px;
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 8px;
  margin-top: 0;
}

.curricular-item span {
  color: var(--darker-gray);
}

@media screen and (max-width: 728px) {
  .course-show {
    flex-direction: column;
  }

  .course-playlist {
    width: 100% !important;
  }

  .course-topics-list-block {
    padding-left: 8px;
    padding-right: 8px;
    width: calc(100% - 16px);
  }

  .course-image {
    min-height: unset;
  }

  .course-container {
    flex-direction: column;
  }

  .course-info {
    margin-left: 0;
    width: calc(92% - 24px);
    margin-bottom: 12px;
  }

  .course-title {
    max-width: unset;
  }

  .course-get {
    width: calc(95% - 24px);
    margin-right: 0;
  }

  .course-body-container {
    flex-direction: column-reverse;
    width: calc(100% - 12px);
    margin: 6px;
    padding: 0;
  }

  .course-body-text {
    width: 100%;
    padding: 0;
  }

  .course-body-info {
    width: 100%;
    margin: 0;
    padding: 0;
    margin-top: 18px;
  }

  .course-teacher, .course-institute {
    flex-direction: column;
  }

  .course-teacher img, .course-institute {
    place-self: center;
    margin-bottom: 12px;
  }
}