.login-redirects {
  color: var(--darker-gray);
  display: flex;
  flex-direction: column;
  width: 80%;
  font-size: 17px;
  position: absolute;
  bottom: -56px;
}

.login-redirects a {
  padding: 8px;
  padding-top: 4px;
  padding-bottom: 4px;
  user-select: none;
  color: var(--darker-gray);
  text-decoration: none;
}

.login-redirects a span {
  color: var(--blue);
  font-weight: 500;
}

.login-error {
  color: crimson !important;
}

.login-form-container {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
}

@media only screen and (max-width: 930px) {
  .login-redirects {
    width: 95%;
  }
}