.error-page {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.error-page div {
  margin: 16px;
  background: white;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 16px;
}

.error-page h2, .error-page p {
  margin-top: 8px;
  margin-bottom: 8px;
}

.error-page p {
  color: var(--darker-gray);
  font-size: 18px;
}